













































import { Content } from "@/FileData";
import Vue from "vue";
import { getFile, deleteFile } from "../sdk";
type LoadingContent = {
  status: "Loading";
};
type ErrorContent = {
  status: "Error";
};
type LoadedContent = {
  status: "Complete";
  content: Content;
};

type DetailsData = {
  current: LoadingContent | ErrorContent | LoadedContent;
};

export default Vue.extend({
  data() {
    return {
      current: { status: "Loading" },
    } as DetailsData;
  },
  created() {
    this.loadContent();
  },
  methods: {
    async loadContent() {
      try{
        const res = await getFile(this.$route.params.id)
        this.current = {
          status: 'Complete',
          content: res
        }
      }
      catch(err){
        console.error(err)
        this.current.status = "Error";
      }
    },
    async deleteItem(){
      await deleteFile(this.$route.params.id)
      this.$router.push({name:'Home'})
    }
  },
});
